import factory from 'utils/factory';

const Store = () => {
    let store = window.localStorage.getItem('store');
    if (!store) {
        store = factory.storeName('article');
        window.localStorage.setItem('store', store);
    }
    return store;
};

export default {
    database: 'yiqian',
    version: 1,
    store: Store(),
    key: {keyPath: 'id', autoIncrement: true},
    indexs: [{
        key: 'title', unique: false
    }, {
        key: 'tags', unique: false
    }]
};
